
import {
    React,
    connect,
    Link,
    reduxForm,
    Panel,
    PanelHeader,
    formatDate,
    moment,
    getTglSystem,
  } from "../../../components/helpers/library";
import HeadLaporanAuthorization from "./HeadLaporanTransaksi.jsx";
import { getLaporanOtorisasi } from '../../../actions/databarang_action.jsx'
  
  
  class LaporanOtorisasi extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        isLoading: false,
        export: false
      };
      this.handleSubmit = this.handleSubmit.bind(this);
    }
  
    handleSubmit(data) {
      let data_post = {
        tgl_awal: formatDate(moment(new Date(data.tgl_awal)).format('YYYY-MM-DD')),
        tgl_akhir: formatDate(moment(new Date(data.tgl_akhir)).format('YYYY-MM-DD')),
      };
      this.props.dispatch(getLaporanOtorisasi(data_post.tgl_awal, data_post.tgl_akhir))
    }
    
    render() {
      return (
        <div>
          <ol className="breadcrumb float-xl-right">
            <li className="breadcrumb-item">
              <Link to="#">Laporan</Link>
            </li>
            <li className="breadcrumb-item active">Laporan Otorisasi</li>
          </ol>
          <h1 className="page-header">Laporan Otorisasi </h1>
          <Panel>
            <PanelHeader>Laporan Otorisasi</PanelHeader>
            <br />
            {/* Laporan Otorisasi */}
                  <div className="container">
                      <HeadLaporanAuthorization
                      dataAuthorization={this.props.dataAuthorization}
                      onSubmit={(data) => this.handleSubmit(data)}
                      export={this.state.export}
                      />
            </div>
            <br />
            {/* End Tambah Laporan Otorisasi  */}
          </Panel>
        </div>
      );
    }
  }
  
  LaporanOtorisasi = reduxForm({
    form: "LaporanOtorisasi",
    enableReinitialize: true,
  })(LaporanOtorisasi);
  export default connect((state) => {
    return {
      dataAuthorization: state.databarang.dataAuthorization,
      initialValues: {
        tgl_awal: getTglSystem(),
        tgl_akhir: getTglSystem(),
      },
    };
  })(LaporanOtorisasi);