import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { reduxForm } from "redux-form";
import { Panel, PanelHeader } from "../../../../components/panel/panel.jsx";
import FIlterLaporanSumary from "./FIlterLaporanSumary.jsx";
import {
  formatDate,
  localStorageencryp,
} from "../../../../components/helpers/function.jsx";
import * as moment from "moment-timezone";
import { postData } from "../../../../config/axios.jsx";
import { ToastNotification } from "../../../../components/helpers/notification.jsx";
class FormLaporanBarangSumary extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      export: false,
      LaporanDataBarang: [],
      tgl_awal: new Date(),
      tgl_akhir: new Date(),
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(data) {
    //   console.log(data);
    this.setState({
      isLoading: true,
    });
    let data_post = {
      tanggal: formatDate(moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")),
    };
      
    postData("barang/get/saldobarang", data_post)
    .then((res) => {
      if (res.data.length === 0) {
        ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
        this.setState({
          isLoading: false,
          export: false,
        });
      } else {
        localStorageencryp("tgl_laporan", JSON.stringify(data_post));
        ToastNotification("success", "Laporan Barang Summary Tersedia");
        this.setState({
          LaporanDataBarang: res.data,
          isLoading: false,
          export: true,
        })
      }
    })
    .catch((error) => this.errorHandling(error));

    // console.log(data_post);
  }
  errorHandling(err) {
    ToastNotification('info', 'Terjadi Kesalahan Saat Request Data')
    this.setState({
      isLoading: false,
      export: false,
    })
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Barang Summary</li>
        </ol>
        <h1 className="page-header">Laporan Barang Summary </h1>
        <Panel>
          <PanelHeader>Laporan Barang Summary</PanelHeader>
          <br />
          {/* Laporan Barang Sumary */}
          <div className="container">
            <FIlterLaporanSumary
              export={this.state.export}
              LaporanDataBarang={this.state.LaporanDataBarang}
              isLoading={this.state.isLoading}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </div>
          <br />
          {/* End Tambah Laporan Barang Sumary  */}
        </Panel>
      </div>
    );
  }
}
FormLaporanBarangSumary = reduxForm({
  form: "FormLaporanBarangSumary",
  enableReinitialize: true,
})(FormLaporanBarangSumary);
export default connect()(FormLaporanBarangSumary);
