import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../../components/panel/panel.jsx";
import { connect } from "react-redux";
import { reduxForm } from "redux-form";
import { formatDate, localStoragedecryp } from "../../../../components/helpers/function.jsx";
import "react-datepicker/dist/react-datepicker.css";
import {
  NotifError,
  ToastNotification,
} from "../../../../components/helpers/notification.jsx";
// import CetakLaporanDetailBarang from "./CetakLaporanDetailBarang.jsx";
// import FormFilter from "./FormFilter.jsx";
import {  postData } from "../../../../config/axios.jsx";
import HeadFilterLaporan from "./HeadFilterLaporan.jsx";
import moment from "moment";
// import LaporanDetailexel from "./LaporanDetailExel.jsx";
const maptostate = (state) => {
  return {
    hideModal: state.datamaster.modalDialog,
  };
};

class FormLaporanTambahBarang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      export: false,
      kode_baki: [],
      LaporanBarangDetail: false,
      columns: [
        {
          dataField: "_id.kode_barcode",
          text: "Kode Barcode",
        },
        {
          dataField: "_id.kode_gudang",
          text: "Kode Gudang",
        },

        {
          dataField: "_id.kode_toko",
          text: "Kode Baki",
        },
        {
          dataField: "berat_tambah",
          text: "Berat",
        },
        {
          dataField: "barang_data[0].kode_group",
          text: "Kode Group",
        },
        {
          dataField: "barang_data[0].kode_dept",
          text: "Kode Jenis",
        },
        {
          dataField: "barang_data[0].nama_barang",
          text: "Nama Barang",
        },
        {
          dataField: "barang_data[0].berat",
          text: "Berat",
        },
        {
          dataField: "barang_data[0].berat_asli",
          text: "Berat Asli",
        },
        {
          dataField: "barang_data[0].nama_atribut",
          text: "Nama Atribut",
        },
        {
          dataField: "barang_data[0].berat_atribut",
          text: "Berat Atribut",
        },
        {
          dataField: "barang_data[0].harga_jual",
          text: "Harga Jual",
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    let data = JSON.parse(localStoragedecryp("userdata"));
    this.setState({ username: data.user_id });
  }
  handleSubmit(data) {
    let data_post = {
      tgl_awal: formatDate(moment(new Date(data.tgl_awal)).format('YYYY-MM-DD')),
      tgl_akhir: formatDate(moment(new Date(data.tgl_akhir)).format('YYYY-MM-DD')),
    };
    this.setState({ isLoading: true, kode_toko: data.kode_baki });
    postData(`barang/tambah-barangs?tanggal[gte]=${data_post.tgl_awal}&tanggal[lte]=${data_post.tgl_akhir}`)
      .then((res) => {
        if (res.data.length === 0) {
          this.setState({
            export: false,
            LaporanBarangDetail: [],
          });
          ToastNotification("info", "Laporan Tambah Barang Tidak Tersedia");
        } else {
          ToastNotification("success", "Laporan Tambah Barang Tersedia");
          this.setState({
            export: true,
            LaporanBarangDetail: res.data,
          });
          console.log(this.state.LaporanBarangDetail);
        }
      })
      .then(() => {
        this.setState({ isLoading: false });
      })
      .catch((error) => this.errorHandling(error));
  }
  errorHandling(err) {
    NotifError(err);
    this.setState({ isLoading: false });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan Tambah Barang</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Tambah Barang Detail</li>
        </ol>
        <h1 className="page-header">Laporan Tambah Barang</h1>
        <Panel>
          <PanelHeader>Filter Laporan</PanelHeader>
          <br />
          <div className="container">
            <div className="row">
              <div className="col-12">
                <HeadFilterLaporan
                  kode_baki={this.state.kode_baki}
                  export={this.state.export}
                  columns={this.state.columns}
                  data={this.state.LaporanBarangDetail}
                  isLoading={this.state.isLoading}
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
        
            </div>
          </div>
          <br />
        </Panel>
      </div>
    );
  }
}

FormLaporanTambahBarang = reduxForm({
  form: "FormLaporanTambahBarang",
  enableReinitialize: true,
})(FormLaporanTambahBarang);
export default connect(maptostate, null)(FormLaporanTambahBarang);
