import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { localStoragedecryp } from "../../../../../components/helpers/function";
class LaporanBarangSumaryExel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    let total_stock_awal = 0;
    let total_berat_awal = 0;
    let total_stock_in = 0;
    let total_berat_in = 0;
    let total_stock_out = 0;
    let total_berat_out = 0;
    let total_stock_hancur = 0;
    let total_berat_hancur = 0;
    let total_stock_jual = 0;
    let total_berat_jual = 0;
    let total_stock_akhir = 0;
    let total_berat_akhir = 0;
    this.props.data.forEach((item, index) => {
      total_stock_awal = total_stock_awal + item.stock_awal;
      total_berat_awal = total_berat_awal + item.berat_awal;
      total_stock_in = total_stock_in + item.stock_in;
      total_berat_in = total_berat_in + item.berat_in;
      total_stock_out = total_stock_out + item.stock_out;
      total_berat_out = total_berat_out + item.berat_out;
      total_stock_hancur = total_stock_hancur + item.stock_hancur;
      total_berat_hancur = total_berat_hancur + item.berat_hancur;
      total_stock_jual = total_stock_jual + item.stock_jual;
      total_berat_jual = total_berat_jual + item.berat_jual;
      total_stock_akhir = total_stock_akhir + item.stock_akhir;
      total_berat_akhir = total_berat_akhir + item.berat_akhir;
    });
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Barang Summary"
          sheet="Laporan Barang Summary"
          buttonText="Export Exel"
        />
        <table
          rowkey="kode_member"
          id="table-to-xls"
          style={{ display: "none" }}
        >
          <thead>
            <tr>
              <th colSpan="12" style={{ textAlign: "center" }}>
                {" "}
                LAPORAN BARANG TOKO{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="12" style={{ textAlign: "center" }}>
                {" "}
                Tanggal{" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tanggal}{" "}
              </th>
            </tr>

            <tr>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                BAKI
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                JML PTG
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                JUMLAH BERAT
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                TAMBAH PTG
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                TAMBAH BERAT
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                JUAL PTG
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                JUAL BERAT
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                HANCUR BERAT
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                HANCUR PTG
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                TOTAL PTG
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                TOTAL BERAT
              </th>
            </tr>
          </thead>

          <tbody>
          {this.props.data.map((item, index) => (
                <tr key={index}>
                  <td> {item._id.kode_toko}</td>
                  <td> {item.stock_awal}</td>
                  <td> {item.berat_awal}</td>
                  <td> {item.stock_in}</td>
                  <td> {item.berat_in}</td>
                  <td> {item.stock_jual}</td>
                  <td> {item.berat_jual}</td>
                  <td> {item.berat_hancur}</td>
                  <td> {item.stock_hancur}</td>

                  <td> {item.stock_akhir}</td>
                  <td> {item.berat_akhir}</td>
                </tr>
          ))}
          </tbody>
          <tfoot>
            <tr>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                Total
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                {" "}
                {total_stock_awal}{" "}
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                {" "}
                {total_berat_awal}{" "}
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                {" "}
                {total_stock_in}{" "}
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                {" "}
                {total_berat_in}{" "}
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                {" "}
                {total_stock_jual}{" "}
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                {" "}
                {total_berat_jual}{" "}
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                {total_berat_hancur}
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                {total_stock_hancur}
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                {total_stock_akhir}
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                {total_berat_akhir}
              </td>
            </tr>
          </tfoot>
        </table>
      </>
    );
  }
}
export default LaporanBarangSumaryExel;
