export const Gramasi = (value, previousValue) => {
  if (value || "") {
    return value.replace(/[^0-9.]+/g, "");
  }
};
export const Kadar = (value, previousValue) => {
  if (value || "") {
    return value.replace(/[^0-9.K]+/g, "");
  }
};

export const NumberOnly = (value, previousValue) => {
  if (value || "") {
    return value.replace(/[^\d]/g, "");
  }
};
